import React, { Component } from "react";
import { getTeam, updateTeam } from "../../actions/TeamActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";

class UpdateTeam extends Component {
  //set state
  constructor() {
    super();
    this.state = {
      team_id: '',
      team_name: '',
      errors: {}
    };
    
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
    const {
      team_id,
      team_name
    } = nextProps.team;

    this.setState({
      team_id,
      team_name
    });
  }

  componentDidMount() {
    const { team_id } = this.props.match.params;
    console.log('id' + JSON.stringify(team_id));
    this.props.getTeam(team_id, this.props.history);
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();


    const updateTeam = {
      team_id: this.state.team_id,
      team_name: this.state.team_name
    };
   
   // this.props.createTeam(updateTeam, this.props.history);
   
    this.props.updateTeam(this.state.team_id, updateTeam,this.props.history);
  }

  render() {
    const { errors } = this.state;
    return (
      <div className="project">
        <div className="container">
          <div className="row">
            <div className="col-md-8 m-auto">
              <h5 className="display-4 text-center">Update Team form</h5>
              <hr />
              <form onSubmit={this.onSubmit}>
                <div className="form-group">
                  <input
                    type="text"
                    className={classnames("form-control form-control-lg", {
                      "is-invalid": errors.team_name
                    })}
                    placeholder="Team Name"
                    name="team_name"
                    value={this.state.team_name}
                    onChange={this.onChange}
                  />
                  {errors.team_name && (
                    <div className="invalid-feedback">{errors.team_name}</div>
                  )}
                </div>
                <input
                  type="submit"
                  className="btn btn-primary btn-block mt-4"
                />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

UpdateTeam.propTypes = {
  getTeam: PropTypes.func.isRequired,
  updateTeam: PropTypes.func.isRequired,
  team: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  team: state.team.team,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { getTeam, updateTeam }
)(UpdateTeam);
