import React, { Component } from "react";
import "./App.css";
import Dashboard from "./components/Dashboard";
import Header from "./components/Layout/Header";
import "bootstrap/dist/css/bootstrap.min.css";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import AddProject from "./components/Project/AddProject";
import { Provider } from "react-redux";
import store from "./store";
import UpdateProject from "./components/Project/UpdateProject";
import ProjectBoard from "./components/ProjectBoard/ProjectBoard";
import AddProjectTask from "./components/ProjectBoard/ProjectTasks/AddProjectTask";
import UpdateProjectTask from "./components/ProjectBoard/ProjectTasks/UpdateProjectTask";
import Landing from "./components/Layout/Landing";
import Register from "./components/UserManagement/Register";
import Login from "./components/UserManagement/Login";
import jwt_decode from "jwt-decode";
import setJWTToken from "./securityUtils/setJWTToken";
import { SET_CURRENT_USER } from "./actions/types";
import { logout } from "./actions/securityActions";
import SecuredRoute from "./securityUtils/SecureRoute";
import Marki from "./components/Marki/Marki";
import Trackin from "./components/Trackin/Trackin";
import Analytic from "./components/Analytics/Analytic";
import image from './logo.png';
//import products from "./components/Home/products";
import ListProductsComponent from "./components/Products/ListProductsComponent";
import AddProduct from "./components/Products/AddProduct";

import ListTeamComponent from "./components/team/ListTeamComponent";

import AddTeam from "./components/team/AddTeam";
import UpdateTeam from "./components/team/UpdateTeam";

import ListCategoryComponent from "./components/Cat/ListCategoryComponent";
import AddCategory from "./components/Cat/AddCategory";
import UpdateCategory from "./components/Cat/UpdateCategory";

import ListExpenseComponent from "./components/expense/ListExpenseComponent";
import AddExpense from "./components/expense/AddExpense";
import UpdateExpense from "./components/expense/UpdateExpense";

import home from "./components/Home/home";
import contact from "./components/Home/contact";
import price from "./components/Home/price";
import PaymentForm from './components/payment/PaymentForm';

import coinprice from "./components/coinprice"
import practice from "./components/practice/practice";
//import 'bootstrap/dist/css/bootstrap.min.css';
console.log("first jwtToken token");
const jwtToken = localStorage.jwtToken;

if (jwtToken) {
  console.log("jwtToken1 inside token");
  console.log(jwtToken);
  setJWTToken(jwtToken);
  const decoded_jwtToken = jwt_decode(jwtToken);
  store.dispatch({
    type: SET_CURRENT_USER,
    payload: decoded_jwtToken
  });

  const currentTime = Date.now() / 1000;

  if (decoded_jwtToken.exp < currentTime) {
    store.dispatch(logout());
    window.location.href = "/";
  }
}else{
  console.log("jwtToken3 else token");
}
console.log("jwtToken2 out token");
console.log(jwtToken);
class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
                  <div className="App">
                   
                <Header />
                
            <br />
            {
              //Public Routes
            }

            <Route exact path="/" component={Landing} />
            <Route exact path="/register" component={Register} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/home" component={home} />
            <Route exact path="/contact" component={contact} />
            <Route exact path="/price" component={price} />
            <Route exact path="/PaymentForm" component={PaymentForm} />
            <Route exact path="/coinprice" component={coinprice} />
            <Route exact path="/practice" component={practice} />
            {
              //Private Routes
            }
            <Switch>
              <SecuredRoute exact path="/dashboard" component={Dashboard} />
              <SecuredRoute exact path="/marki" component={Marki} />
              <SecuredRoute exact path="/trackin" component={Trackin} />
              <SecuredRoute exact path="/analytic" component={Analytic} />
              <SecuredRoute exact path="/listTeamComponent" component={ListTeamComponent} />
              <SecuredRoute exact path="/addProject" component={AddProject} />
              <SecuredRoute exact path="/addTeam" component={AddTeam} />

              <SecuredRoute exact path="/listCategoryComponent" component={ListCategoryComponent} />
              <SecuredRoute exact path="/addCategory" component={AddCategory} />
              <SecuredRoute exact path="/updateCategory/:id" component={UpdateCategory} />

              <SecuredRoute exact path="/ListExpenseComponent" component={ListExpenseComponent} />
              <SecuredRoute exact path="/addExpense" component={AddExpense} />
              <SecuredRoute exact path="/updateExpense/:id" component={UpdateExpense} />

             
              <SecuredRoute exact path="/ListProductsComponent" component={ListProductsComponent} />
              <SecuredRoute exact path="/addProduct" component={AddProduct} />
              

              <SecuredRoute
                exact
                path="/updateTeam/:team_id"
                component={UpdateTeam}
              />
              <SecuredRoute
                exact
                path="/updateProject/:id"
                component={UpdateProject}
              />
              <SecuredRoute
                exact
                path="/projectBoard/:id"
                component={ProjectBoard}
              />
              <SecuredRoute
                exact
                path="/addProjectTask/:id"
                component={AddProjectTask}
              />
              <SecuredRoute
                exact
                path="/updateProjectTask/:backlog_id/:pt_id"
                component={UpdateProjectTask}
              />
            </Switch>
            
          </div>

        </Router>
      </Provider>
    );
  }
}

export default App;
