/* eslint-disable import/no-anonymous-default-export */
import { GET_ROLES,GET_ROLE } from "../actions/types";

const initialState = {
  roles: [],
  role: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_ROLES:
      return {
        ...state,
        roles: action.payload
      };
      case GET_ROLE:
        return {
          ...state,
          role: action.payload
      };
    default:
      return state;
  }
}
