import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createProduct, getRoles} from "../../actions/ProductActions";

class AddProduct extends Component {
  constructor() {
    super();
    const userDetails = JSON.parse(localStorage.getItem('userDetails'));
    var usid = userDetails.id;

    this.state = {
      id: '',
      user: {id:usid},
      role: {id:0, name:''},
      errors: {}
    };

    this.changeRoleHandler = this.changeRoleHandler.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  componentDidMount() {

    this.props.getRoles();
  }
  //life cycle hooks
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      
      this.setState({ errors: nextProps.errors });
     
    }
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }
  changeRoleHandler(event) {   
    const name = {id :event.target.value};
  
    this.setState({role:name});  
}
  onSubmit(e) {
    e.preventDefault();
    const newProduct = {
      role: this.state.role,
      user: this.state.user
    };
     console.log(newProduct);
     console.log(this.props.history);
   this.props.createProduct(newProduct,this.props.history);
  }

  render() {
 
    const { roles } = this.props.role;
   
    let optionList  =
    roles.map( (role) =>
                <option value={role.id} key={role.id} >
                    {role.name}
                </option>
            )

    return (
      <div>
        <div className="product">
          <div className="container">
            <div className="row">
              <div className="col-md-8 m-auto">
                <h5 className="display-4 text-center">Create product form</h5>
                <hr />
                <form onSubmit={this.onSubmit}>
                 
                <br></br>
                <div className="form-group">
                  <select
                    className="form-control form-control-lg"
                    name="role_id"
                    value={this.state.role.id}
                    onChange={this.changeRoleHandler}
                  >
                     <option value={-1}> Choose a Product:</option>   
                        {optionList}
                  </select>
                </div>

                  <input
                    type="submit"
                    className="btn btn-primary btn-block mt-4"
                  />
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AddProduct.propTypes = {
  createProduct: PropTypes.func.isRequired,
  role: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors,
  role: state.role
});

export default connect(
  mapStateToProps,
  { createProduct,getRoles }
)(AddProduct);
