import React, { Component } from 'react'
import { getProducts, deleteProduct,getRoles } from "../../actions/ProductActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

class ListProductsComponent extends Component {
    

    componentDidMount() {
      const userDetails1 = JSON.parse(localStorage.getItem('userDetails'));
    
     
      const user_id =userDetails1.id;

     // this.props.match.params;
      
      this.props.getProducts(user_id);
    //  this.props.getRoles(user_id);
    }
    addProduct(){
      this.props.history.push('/addProduct');
    }
    onDeleteClick = id => {
      this.props.deleteProduct(id);
    };
   

    render() {
    
      const { products } = this.props.product;
     
      const userDetails = JSON.parse(localStorage.getItem('userDetails'));
      console.log(products);
     
      let listRoles = userDetails.roles.map((lstrl) => 
      <li key={lstrl.toString()}>{lstrl}</li>)
     
      //get roles from table 
      let listRoles1 = products.map((product1) => 
      <li key={product1.id}>{product1.role.name}</li>)
      const listRoles2 = products.map(product1 => product1.role.name)
      console.log("products");
      console.log(products);
      console.log(listRoles1);
      console.log(listRoles2);


    const trackrole = listRoles2.includes("ROLE_TRACK") ? 'track on' : 'track off';
    const markirole = listRoles2.includes("ROLE_MARKI") ? 'mark on' : 'mark off';;
    const expenserole = listRoles2.includes("ROLE_EXPENSE") ? 'expense on' : 'expense off';;


    // trackrole = products.role.name("ROLE_TRACK");
    // markirole =products.role.name("ROLE_MARKI");
    // expenserole =products.role.name("ROLE_EXPENSE");

      return (

<div className = "container">

                   
<Link to="/addProduct" className="btn btn-lg btn-outline-primary">
    Create a Product
  </Link>
  <br/>
  <p>Products</p>
  <br/>
  <br />
              {listRoles}

              <br />
              <hr />
              {listRoles1}
              <hr />
              {trackrole}<br/>
              {markirole}<br/>
              {expenserole}
              <hr />
              

<table className = "table table-striped table-bordered">

    <thead>
        <tr>
            <th> User</th>
            <th> Role</th>
            <th> Actions</th>
        </tr>
    </thead>
    <tbody>
        {
            products.map(
                product => 
                <tr key = {product.id}>
                     <td> { product.user.username} </td>
                     <td> { product.role.name} </td>   
                     <td>
                         <button style={{marginLeft: "10px"}}  onClick={this.onDeleteClick.bind(this, product.id)} className="btn btn-md btn-outline-danger">
                         <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash" viewBox="0 0 16 16">
                          <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                          <path fillRule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
                          </svg>Delete </button>
                        
                         <Link to={`/updateProduct/${product.id}`} className="btn btn-ld btn-outline-primary mr4">
                          update
                         </Link>               
                     </td>
                </tr>
            )
        }
    </tbody>
</table>

</div>
      );
    }
  }
  
  ListProductsComponent.propTypes = {
    product: PropTypes.object.isRequired,
    getProducts: PropTypes.func.isRequired,
    deleteProduct: PropTypes.func.isRequired
  };
  
  const mapStateToProps = state => ({
    product: state.product,
    role: state.role
  });
  
  export default connect(
    mapStateToProps,
    { getProducts, deleteProduct, getRoles}
  )(ListProductsComponent);