import axios from "axios";
import { GET_ERRORS, GET_EXPENSES, GET_EXPENSE, DELETE_EXPENSE } from "./types";


export const getExpenses = () => async dispatch => {
   const res = await axios.get("/api/expense/all");
   dispatch({
     type: GET_EXPENSES,
     payload: res.data
   });
};

export const getExpense = (id, history) => async dispatch => {
  try {
    console.log('get expense' + JSON.stringify(id));
    const res = await axios.get(`/api/expense/${id}`);
    dispatch({
      type: GET_EXPENSE,
      payload: res.data
    });
  } catch (error) {
    history.push("/expense");
  }
};

export const deleteExpense = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the project and all the data related to it"
    )
  ) {
    await axios.delete(`/api/expense/${id}`);
    dispatch({
      type: DELETE_EXPENSE,
      payload: id
    });
  }
};
export const createExpense= (expense, history) => async dispatch => {
  try {
    console.log('expense' + JSON.stringify(expense));
    await axios.post("/api/expense", expense);
    //await axios.post(`/api/project/`, project);
    history.push("/listExpenseComponent");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

