import React, { Component } from 'react'
import { getCategories, deleteCategory } from "../../actions/CategoryActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

class ListCategoryComponent extends Component {
    componentDidMount() {
      
      this.props.getCategories();
      
    }
    addCategory(){
      this.props.history.push('/addCategory');
    }
    onDeleteClick = id => {
      this.props.deleteCategory(id);
    };
   

    render() {
      
      const { categories } = this.props.category;
      
      return (

<div className = "container">

                   
<Link to="/addCategory" className="btn btn-lg btn-outline-primary">
    Create a Category
  </Link>
  <br/>
  <br/>
<table className = "table table-striped table-bordered">

    <thead>
        <tr>
            <th> Category id</th>
            <th> Category Name</th>
            <th> Actions</th>
        </tr>
    </thead>
    <tbody>
        {
            categories.map(
                category => 
                <tr key = {category.id}>
                     <td> { category.id} </td>
                     <td> { category.name} </td>   
                     <td>
                         <button style={{marginLeft: "10px"}}  onClick={this.onDeleteClick.bind(this, category.id)} className="btn btn-md btn-outline-danger">
                         <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash" viewBox="0 0 16 16">
                          <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                          <path fillRule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
                          </svg>Delete </button>
                        
                         <Link to={`/updateCategory/${category.id}`} className="btn btn-ld btn-outline-primary mr4">
                          update
                         </Link>               
                     </td>
                </tr>
            )
        }
    </tbody>
</table>

</div>
      );
    }
  }
  
  ListCategoryComponent.propTypes = {
    category: PropTypes.object.isRequired,
    getCategories: PropTypes.func.isRequired,
    deleteCategory: PropTypes.func.isRequired
  };
  
  const mapStateToProps = state => ({
    category: state.category
  });
  
  export default connect(
    mapStateToProps,
    { getCategories, deleteCategory }
  )(ListCategoryComponent);