import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createExpense} from "../../actions/ExpenseActions";
import { getCategories } from "../../actions/CategoryActions";
import { getTeams } from "../../actions/TeamActions";
import classnames from "classnames";
import "react-datepicker/dist/react-datepicker.css";

class AddExpense extends Component {
  emptyItem = {
    description : '' ,
    name: '',
    revenue:0,
    expensedate : '',
    location : '',
    category : {id:1 , name:'Travel'},
    team : {team_id:1,team_name:'test'}
}
  constructor() {
    super();

    this.state = {
      id: '',
      description: '',
      name: '',
      revenue:0,
      expensedate : '',
      location : '',
      expteam:{team_id:'',team_name:''},
      expvalue:{id:'' , name:''},
      errors: {}
    };
    this.changeExpTeamHandler = this.changeExpTeamHandler.bind(this);
    this.handleExpDateChange = this.handleExpDateChange.bind(this);
    this.changeExpCatHandler = this.changeExpCatHandler.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  componentDidMount() {
    console.log('expense didmount');
    this.props.getCategories();
    this.props.getTeams();

  }
  //life cycle hooks
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }
  changeExpTeamHandler(event) {   
    const name = {team_id :event.target.value};
    
    this.setState({expteam:name});  
}
changeExpCatHandler(event) {   
  const name = {id :event.target.value};

  this.setState({expvalue:name});  
}
handleExpDateChange(date){
  this.setState({expdate:date});
}
  onSubmit(e) {
    e.preventDefault();
    const newExpense = {
      description: this.state.description,
      name: this.state.name,
      revenue: this.state.revenue,
      location:this.state.location,
      team: this.state.expteam,
      category: this.state.expvalue,
      expensedate: this.state.expensedate
      /*,
      expvalue: this.state.expvalue,
      expdate: this.state.expdate
      */
    };
     //let expense = {description: this.state.expdesc, category: this.state.expvalue,team:this.state.expteam, expensedate: this.state.expdate, location: this.state.explocation};
       
     console.log(newExpense);
   this.props.createExpense(newExpense,this.props.history);
  }

  render() {
    const { errors } = this.state;
    console.log(this.props);
    const { teams } = this.props.team;
  //  const {Teams} = this.state.team;
    const { categories } = this.props.category;
   
    let optionList  =
            categories.map( (category) =>
                <option value={category.id} key={category.id} >
                        {category.name}    
                </option>
            )

    let optionListTeam  =
    teams.map( (team) =>
        <option value={team.team_id} key={team.team_id} >
                    {team.team_name}
        </option>
    )
   
    return (
      <div>
        <div className="project">
          <div className="container">
            <div className="row">
              <div className="col-md-8 m-auto">
                <h5 className="display-4 text-center">Create Expense form</h5>
                <hr />
                <form onSubmit={this.onSubmit}>
                 
                <div className="form-group">
                    <input
                      type="text"
                      className={classnames("form-control form-control-lg", {
                        "is-invalid": errors.name
                      })}
                      placeholder=" Name"
                      name="name"
                      value={this.state.name}
                      onChange={this.onChange}
                    />
                   
                 
                  </div>
                  <br></br>

                  <div className="form-group">
                    <input
                      type="text"
                      className={classnames("form-control form-control-lg", {
                        "is-invalid": errors.description
                      })}
                      placeholder="description Name"
                      name="description"
                      value={this.state.description}
                      onChange={this.onChange}
                    />
                    {errors.description && (
                      <div className="invalid-feedback">
                        {errors.description}
                      </div>
                    )}
                 
                  </div>
                  <br></br>

                  <div className="form-group">
                    <input
                      type="text"
                      className={classnames("form-control form-control-lg", {
                        "is-invalid": errors.revenue
                      })}
                      placeholder=" Revenue"
                      name="revenue"
                      value={this.state.revenue}
                      onChange={this.onChange}
                    />
                   
                 
                  </div>
                  <br></br>

                  <div className="form-group">
                  <select
                    className="form-control form-control-lg"
                    name="catid"  value={this.state.expvalue.id} onChange={this.changeExpCatHandler}
                  >
                     <option value={-1}> Choose a Category:</option>   
                        {optionList}
                  </select>
                </div>

                <br></br>

                  <div className = "form-group">

                  <select
                    className="form-control form-control-lg"
                    name="team_id"  value={this.state.expteam.team_id} onChange={this.changeExpTeamHandler}
                  >
                     <option value={-1}> Choose a team:</option>   
                        {optionListTeam}
                  </select>
                  </div>
                  <br></br>
                  <div className="form-group">
                  <input
                    type="date"
                    className="form-control form-control-lg"
                    name="expensedate"
                    value={this.state.expensedate}
                    onChange={this.onChange}
                  />
                  </div>
                  <br></br>
                  <div className="form-group">
                    <input
                      type="text"
                      className={classnames("form-control form-control-lg", {
                        "is-invalid": errors.location
                      })}
                      placeholder="location Name"
                      name="location"
                      value={this.state.location}
                      onChange={this.onChange}
                    />
                    {errors.location && (
                      <div className="invalid-feedback">
                        {errors.location}
                      </div>
                    )}
                 
                  </div>
                  <input
                    type="submit"
                    className="btn btn-primary btn-block mt-4"
                  />
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AddExpense.propTypes = {
  createExpense: PropTypes.func.isRequired,
  team: PropTypes.object.isRequired,
  category: PropTypes.object.isRequired,
  getTeams: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors,
  team: state.team,
  category: state.category
});

export default connect(
  mapStateToProps,
  { createExpense,getCategories,getTeams }
)(AddExpense);
