import React, { Component } from "react";

import { updateExpense, getExpense} from "../../actions/ExpenseActions";
import { getCategories } from "../../actions/CategoryActions";
import { getTeams } from "../../actions/TeamActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";

class UpdateExpense extends Component {
  //set state

  constructor() {
    super();
    this.state = {
      id: '',
      description: '',
      name: '',
      revenue: '',
      expensedate : '',
      location : '',
      team:{team_id:'',team_name:''},
      category:{id:'' , name:''},
      errors: {}
    };
    this.changeExpTeamHandler = this.changeExpTeamHandler.bind(this);
    this.changeExpCatHandler = this.changeExpCatHandler.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
    const {
    id,
    description,
    name,
    revenue,
    expensedate,
    location,
    category,
    team
    } = nextProps.expense;

    this.setState({
    id,
    description,
    name, 
    revenue,
    expensedate,
    location,
    category,
    team
    });
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    console.log('id' + JSON.stringify(id));
   
    this.props.getExpense(id, this.props.history);
    this.props.getCategories();
    this.props.getTeams();
    /*
    this.setState({ 
      
    // expvalue:{id:expense.category.id },
    //  expteam:this.state.team
    
  }); 
*/
  }

onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
}
changeExpTeamHandler(event) {   
    const name = {team_id :event.target.value};
    this.setState({team:name});  
}
changeExpCatHandler(event) {   
  const name = {id :event.target.value};
  this.setState({category:name});  
}
onSubmit(e) {
    e.preventDefault();

    const updateExpense = {
      id:this.state.id,
      description: this.state.description,
      name: this.state.name,
      revenue: this.state.revenue,
      location:this.state.location,
      team: this.state.team,
      category: this.state.category,
      expensedate: this.state.expensedate
    
};
  
  
  console.log('update' );

    this.props.updateExpense(this.state.id, updateExpense,this.props.history);
  }

  render() {
    const { errors } = this.state;
    console.log('update expense');
    console.log(this.state);

    const { categories } = this.props.category;
    const { teams } = this.props.team;

    let optionListTeam  =
    teams.map( (team) =>
        <option value={team.team_id} key={team.team_id} >
            {team.team_name}
        </option>
    )
    let optionList  =
            categories.map( (category) =>
                <option value={category.id} key={category.id} >
                      {category.name}
                </option>
            )
 if (this.state.id == null){
   return null;
 }

return (
<div className="project">
<div className="container">
<div className="row">
<div className="col-md-8 m-auto">
<h5 className="display-4 text-center">Update Expense form</h5>
<hr />
<form onSubmit={this.onSubmit}>

<div className="form-group">
<input
type="text"
className={classnames("form-control form-control-lg", {
"is-invalid": errors.name
})}
placeholder="Name"
name="name"
value={this.state.name}
onChange={this.onChange}
/>
</div>
<br></br>

<div className="form-group">
<input
type="text"
className={classnames("form-control form-control-lg", {
"is-invalid": errors.description
})}
placeholder="descrip Name"
name="description"
value={this.state.description}
onChange={this.onChange}
/>
{errors.description && (
<div className="invalid-feedback">{errors.description}</div>
)}
</div>
<br></br>

<div className="form-group">
<input
type="text"
className={classnames("form-control form-control-lg", {
"is-invalid": errors.revenue
})}
placeholder="Revenue"
name="revenue"
value={this.state.revenue}
onChange={this.onChange}
/>


</div>
<br></br>

<div className="form-group">
<input
type="text"
className={classnames("form-control form-control-lg", {
"is-invalid": errors.location
})}
placeholder="location Name"
name="location"
value={this.state.location}
onChange={this.onChange}
/>
{errors.explocation && (
<div className="invalid-feedback">
{errors.location}
</div>
)}

</div>
<br></br>
<div className="form-group">
<input
type="date"
className="form-control form-control-lg"
name="expensedate"
value={this.state.expensedate}
onChange={this.onChange}
/>
</div>
<br></br>

<div className="form-group">
<select
className="form-control form-control-lg"
name="catid"  value={this.state.category.id || ''} onChange={this.changeExpCatHandler}
>
<option value={-1}> Choose a Category:</option>   
{optionList}
</select>


</div>


<br></br>
<div className = "form-group">

<select
className="form-control form-control-lg"
name="team_id"  value={this.state.team.team_id} onChange={this.changeExpTeamHandler}
>
<option value={-1}> Choose a Team:</option>  
{optionListTeam}
</select>
</div>

<input
type="submit"
className="btn btn-primary btn-block mt-4"
/>
</form>
</div>
</div>
</div>
</div>
);
  }
}

UpdateExpense.propTypes = {
  getExpense: PropTypes.func.isRequired,
  updateExpense: PropTypes.func.isRequired,
  team: PropTypes.object.isRequired,
  category: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  expense: state.expense.expense,
  team: state.team,
  category: state.category,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { getExpense, updateExpense,getCategories,getTeams }
)(UpdateExpense);
