import axios from "axios";
import { GET_ERRORS, GET_PRODUCTS,GET_ROLES, GET_PRODUCT, DELETE_PRODUCT } from "./types";


export const getProducts= (user_id) => async dispatch => {

  // const res = await axios.get("/api/userroles/all");
  const res = await axios.get(`/api/userroles/all/${user_id}`);

   dispatch({
     type: GET_PRODUCTS,
     payload: res.data
   });
};
export const getRoles = (id) => async dispatch => {

   const res = await axios.get("/api/userroles/allRoles");
  // const res = await axios.get(`/api/userroles/all/${id}`);
   dispatch({
     type: GET_ROLES,
     payload: res.data
   });
};
export const getProduct = (id, history) => async dispatch => {
  try {
    
    const res = await axios.get(`/api/userroles/${id}`);
    dispatch({
      type: GET_PRODUCT,
      payload: res.data
    });
  } catch (error) {
    history.push("/product");
  }
};

export const deleteProduct = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the project and all the data related to it"
    )
  ) {

    await axios.delete(`/api/userroles/${id}`);
    dispatch({
      type: DELETE_PRODUCT,
      payload: id
    });
  }
};
export const createProduct= (product, history) => async dispatch => {
  try {

    await axios.post("/api/userroles", product);
    //await axios.post(`/api/project/`, project);
    history.push("/listProductsComponent");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateProduct = (
  id,
  product,
  history
) => async dispatch => {
  try {
    

   
    await axios.patch(`/api/userroles/${id}`, product);

   // await axios.put(`/api/category/${team_id}`, team);
   history.push("/listProductsComponent");
    
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
   
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};