import axios from "axios";
import { GET_ERRORS, GET_CATEGORIES, GET_CATEGORY, DELETE_CATEGORY } from "./types";


export const getCategories = () => async dispatch => {

   const res = await axios.get('/api/category/all');

   dispatch({
     type: GET_CATEGORIES,
     payload: res.data
   });
};

export const getCategory = (id, history) => async dispatch => {
  try {
    
    const res = await axios.get(`/api/category/${id}`);
    dispatch({
      type: GET_CATEGORY,
      payload: res.data
    });
  } catch (error) {
    history.push("/category");
  }
};

export const deleteCategory = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the project and all the data related to it"
    )
  ) {
    await axios.delete(`/api/category/${id}`);
    dispatch({
      type: DELETE_CATEGORY,
      payload: id
    });
  }
};
export const createCategory= (category, history) => async dispatch => {
  try {
    
    await axios.post("/api/category", category);
    //await axios.post(`/api/project/`, project);
    history.push("/listCategoryComponent");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateCategory = (
  id,
  category,
  history
) => async dispatch => {
  try {
    
 
   
    await axios.patch(`/api/category/${id}`, category);

   // await axios.put(`/api/category/${team_id}`, team);
   history.push("/listCategoryComponent");

    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {

    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};