import React, { Component } from "react";
import { getCategory, updateCategory } from "../../actions/CategoryActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";

class UpdateCategory extends Component {
  //set state
  constructor() {
    super();
    this.state = {
      id: '',
      name: '',
      errors: {}
    };
    
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
    const {
      id,
      name
    } = nextProps.category;

    this.setState({
      id,
      name
    });
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    console.log('id' + JSON.stringify(id));
    this.props.getCategory(id, this.props.history);
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();


    const updateCategory = {
      id: this.state.id,
      name: this.state.name
    };
   
   // this.props.createTeam(updateTeam, this.props.history);
   console.log('update' );
   console.log('id' + JSON.stringify( this.state.id));
   console.log('category' + JSON.stringify(updateCategory));
   
    this.props.updateCategory(this.state.id, updateCategory,this.props.history);
  }

  render() {
    const { errors } = this.state;
    console.log(this.state);
    return (
      <div className="project">
        <div className="container">
          <div className="row">
            <div className="col-md-8 m-auto">
              <h5 className="display-4 text-center">Update Category form</h5>
              <hr />
              <form onSubmit={this.onSubmit}>
                <div className="form-group">
                  <input
                    type="text"
                    className={classnames("form-control form-control-lg", {
                      "is-invalid": errors.name
                    })}
                    placeholder="Category Name"
                    name="name"
                    value={this.state.name}
                    onChange={this.onChange}
                  />
                  {errors.name && (
                    <div className="invalid-feedback">{errors.name}</div>
                  )}
                </div>
                <input
                  type="submit"
                  className="btn btn-primary btn-block mt-4"
                />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

UpdateCategory.propTypes = {
  getCategory: PropTypes.func.isRequired,
  updateCategory: PropTypes.func.isRequired,
  category: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  category: state.category.category,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { getCategory, updateCategory }
)(UpdateCategory);
