import React, { Component } from 'react'
import { getExpenses, deleteExpense } from "../../actions/ExpenseActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Moment from 'react-moment';

class ListExpenseComponent extends Component {
    componentDidMount() {
     
      this.props.getExpenses();
   
    }

    onDeleteClick = id => {
      this.props.deleteExpense(id);
    };
   

    render() {
      
      const { expenses } = this.props.expense;
  
      return (

<div className = "container">
                   
<Link to="/addExpense" className="btn btn-lg btn-outline-primary">
    Create a Expense
  </Link>
  <br />
  <br />
<table className = "table table-striped table-bordered">

<thead>
    <tr>
        <th> Expense id</th>
        <th> Description</th>
        <th> Location</th>
        <th> Date</th>
        <th> Category</th>
        <th> Team</th>
        <th> name</th>
        <th> revenue</th>
        <th> Actions</th>
    </tr>
</thead>
    <tbody>
        {
            expenses.map(
                expense => 
                <tr key = {expense.id}>
                      <td> { expense.id} </td>
                      <td> { expense.description} </td> 
                      <td> { expense.location} </td> 
                      <td> <Moment date={expense.expensedate} format="YYYY/MM/DD"/></td> 
                      <td> {expense.category.name} </td> 
                      <td> {expense.team.team_name} </td> 
                      <td> {expense.name != null ? expense.name : " "} </td> 
                      <td> {expense.revenue != null ? expense.revenue : " "} </td> 
                      <td>
                         <button style={{marginLeft: "10px"}}  onClick={this.onDeleteClick.bind(this, expense.id)} className="btn btn-danger">
                         <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash" viewBox="0 0 16 16">
                          <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                          <path fillRule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
                          </svg>Delete </button>
                        
                         <Link to={`/updateExpense/${expense.id}`} className="btn btn-ld btn-outline-primary mr4">
                          update
                        </Link>               
                     </td>
                </tr>
            )
        }
    </tbody>
</table>
</div>
      );
    }
  }
  
  ListExpenseComponent.propTypes = {
    expense: PropTypes.object.isRequired,
    getExpenses: PropTypes.func.isRequired,
    deleteExpense: PropTypes.func.isRequired
  };
  
  const mapStateToProps = state => ({
    expense: state.expense
  });
  
  export default connect(
    mapStateToProps,
    { getExpenses, deleteExpense }
  )(ListExpenseComponent);