import axios from "axios";
import { GET_ERRORS, GET_TEAMS, GET_TEAM, DELETE_TEAM } from "./types";


export const getTeams = () => async dispatch => {
   const res = await axios.get("/api/team/all");
   dispatch({
     type: GET_TEAMS,
     payload: res.data
   });
};

export const getTeam = (team_id, history) => async dispatch => {
  try {
    
    const res = await axios.get(`/api/team/${team_id}`);
    dispatch({
      type: GET_TEAM,
      payload: res.data
    });
  } catch (error) {
    history.push("/team");
  }
};

export const deleteTeam = team_id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the project and all the data related to it"
    )
  ) {
    await axios.delete(`/api/team/${team_id}`);
    dispatch({
      type: DELETE_TEAM,
      payload: team_id
    });
  }
};
export const createTeam= (team, history) => async dispatch => {
  try {
    
    await axios.post("/api/team", team);
    //await axios.post(`/api/project/`, project);
    history.push("/listTeamComponent");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateTeam = (
  team_id,
  team,
  history
) => async dispatch => {
  try {
   
    await axios.patch(`/api/team/${team_id}`, team);
   
   // await axios.put(`/api/team/${team_id}`, team);
   history.push("/listTeamComponent");
   
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
  
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};